:root {
  --primary: #002F44;
  --secundary: #FDC737; 
}

@font-face {
  font-family: 'Nirmala';
  src: local('Nirmala'), url(./fonts/Nirmala.ttf) format('truetype');
}

body {
  margin: 0;
  font-family: 'Nirmala', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Nirmala';
  font-size: small;
  text-align: center;
  min-width: 320px;
}

.Bar {
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 10px;
}

.Bar-Divider {
  margin-top: 5px;
}

.Bar-logo {
  float: left;
  text-align: left;
  margin: auto;
  width: 168px;
}

.Bar-icon {
  float: right;
  text-align: right;
  margin-right: 6px;
  margin-top: 5px;
  width: 100px;
}

.content {
  position: relative;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  max-width: 300px;
}

.limited {
  margin-top: 20px;
}

.info {
  margin-top: 20px;
  margin-bottom: 20px;
}

.bottom {
    position: relative;
    margin: 5px;
}

.top {
  height: 80px;
}

.PasswordPrompt {
  text-align: center;
  margin: 10px;
  min-width: 200px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.footer-report-button {
  padding-right: 10px;
  padding-left: 10px;
  background-color: transparent !important;
  box-shadow: none !important;
}

.footer-button {
  padding-right: 10px;
  padding-left: 10px;
}

.back-button {
  border-radius: 23
}

.send-button {
  margin-top: '10px'
}


@media only screen and (hover: none) and (pointer: coarse) {
  .mobile-button {
    min-height: 44px;
    font-size: 0.975rem !important;
  }
  .content {
    display: none;
  }
  .footer-report-button {
    width: 100%;
    background-color: #002F44 !important;
    color: #fff !important;
  }
  .footer-button {
    width: 100%;
  }
  .version-text {
    margin-top: 15px !important;
    display: inline-block;
  }
}